import { Reducer } from 'redux';

import { Actions, ActionTypes } from '../actions';
import { createAttempt } from '../models';
import { StoreState } from '../types';

export const reducer: Reducer = (state: StoreState, action: Actions): StoreState => {
  if (action.type === ActionTypes.RESET_ATTEMPT) {
    return {
      ...state,
      current: {
        ...state.current,
        attempt: createAttempt(),
      }
    };
  }

  if (action.type === ActionTypes.RECORD_ATTEMPT) {
    const { current: { attempt } } = state;

    if (!attempt) {
      return state;
    }

    return {
      ...state,
      current: {
        ...state.current,
        attempt: createAttempt(),
        session: {
          ...state.current.session,
          results: [...state.current.session.results, attempt.createResult(action.payload)]
        },
      }
    };
  }

  // TODO
  /*
  if (action.type === ActionTypes.DELETE_RESULT) {
    const results = state.results.slice();
    results.splice(action.payload.index, 1);

    return {
      ...state,
      results,
    };
  }
  */

  // TODO: split to sync dedicated reducer and combine
  if (action.type === ActionTypes.START_RECORDS_UPLOAD) {
    return {
      ...state,
      sync: {
        ...state.sync,
        isSyncing: true,
      },
    };
  }

  if (action.type === ActionTypes.FINISH_RECORDS_UPLOAD) {
    return {
      ...state,
      sync: {
        ...state.sync,
        isSyncing: false,
      },
    };
  }

  if (action.type === ActionTypes.UPDATE_SYNC_SPREADSHEET_ID) {
    return {
      ...state,
      sync: {
        ...state.sync,
        spreadsheetId: action.payload.spreadsheetId,
      },
    };
  }

  if (action.type === ActionTypes.UPDATE_IS_AUTHED) {
    return {
      ...state,
      auth: {
        isAuthed: action.payload.isAuthed,
      },
    };
  }

  if (action.type === ActionTypes.CREATE_NEW_SESSION) {
    return {
      ...state,
      current: {
        ...state.current,
        session: {
          name: 'TODO',
          results: [],
        }
      },
      results: [
        ...state.results,
        {
          isSynced: false,
          session: state.current.session,
        },
      ]
    };
  }

  return state;
};


